export function PrikazListener(user) {
    const word = `<html xmlns:o='urn:schemas-microsoft-com:office:office
      xmlns:w='urn:schemas-microsoft-com:office:word'
      xmlns='http://www.w3.org/TR/REC-html40'>
      <head>
      <meta charset='utf-8'>
      <title>  </title>
        <style>
          p {
            font-family: 'Times New Roman', Times, serif; 
            font-size: 14pt;
          } 
        </style>
      </head>
      <body>
      <center>
      <h1>  </h1>
      </center>
      <hr></hr>
      <center>
                
      </center>
      <h3>  </h3>
      <p>  - ${user.fiolistener}</p>
      <p>  - ${new Date(user.datebirth).toLocaleDateString()}</p>
      <p> - ${user.education}</p>
      <p>Email  - ${user.email}</p>
      <p>   - ${user.phone}</p>
      <hr></hr>
      <p>   ,     ,       ,           ().
           ()     .
      </p>
      <p>
       ,            , ,                  27.07.2006  152-         .     ,     .
      </p>
      <p>_______________________________________________</p>
      </body>
      </html>`;
    let source =
      "data:application/vnd.ms-word;charset=utf-8," +
      encodeURIComponent(word);
    let fileDownload = document.createElement("a");
    document.body.appendChild(fileDownload);
    fileDownload.href = source;
    fileDownload.download = `   ${user.fiolistener}.doc`;
    fileDownload.click();
    document.body.removeChild(fileDownload);
  }