export function PrikazZachslenie(datePrikaz, numberPrikaz, group) {
  const iterationUsers = (coll) => {
    var result = "<ol>";
    for (const user of group.user) {
      result += `<li>${user.fiolistener}</li>`;
    }
    result = `${result}</ol>`;
    return result;
  };
  const word = `<html xmlns:o='urn:schemas-microsoft-com:office:office
        xmlns:w='urn:schemas-microsoft-com:office:word'
        xmlns='http://www.w3.org/TR/REC-html40'>
        <head>
          <meta charset='utf-8'>
          <title>  </title>
          <style>
            p {
              font-family: 'Times New Roman', Times, serif; 
              font-size: 14pt;
            }
            li {
              font-family: 'Times New Roman', Times, serif; 
              font-size: 14pt;
            }
          </style>
        </head>
        <body>
          <center>
            <h1>    PRO</h1>
            <h3> : ${datePrikaz}.  ${numberPrikaz}</h3>
            <h4>          ${group.namegroup}</h4>
          </center>
          <p>      ,    PRO  </p>
            <hr></hr>
              ${iterationUsers()}
            <hr></hr>
            <center>
              <h1></h1>
            </center>
            <ol>
              <li>   "${group.namegroup}"</li>
              <li>
                          
                " PRO"     "${group.namegroup}"
              </li>
              <li>  "${group.namegroup}"     PRO  ${datePrikaz}.</li>
              <li>           .</li>
              <li>             .</li>
              <li>
                                   "${group.namegroup}"
                         .
              </li>
              <p>  </p>
              <p> : (- ) _____________</p>
              <p> 1: (- 1) _____________</p>
              <p> 2: (- 2) _____________</p>
              <p> 3: (- 3) _____________</p>
            </ol>
        </body>
      </html>`;
  let source =
    "data:application/vnd.ms-word;charset=utf-8," + encodeURIComponent(word);
  let fileDownload = document.createElement("a");
  document.body.appendChild(fileDownload);
  fileDownload.href = source;
  fileDownload.download = `  .doc`;
  fileDownload.click();
  document.body.removeChild(fileDownload);
}
